<template>
  <div>
    <div>log in to a book room: </div>
    <input type="text" v-model="code" placeholder="enter room code"/>
    <button type="click">enter</button>
    <BookRoom v-bind:roomCode="code" v-bind:bookComments="bookComments"></BookRoom>
  </div>
</template>

<script>
import BookRoom from './BookRoom'

export default {
  name: 'ReadTogether',
  components: {
    BookRoom
  },
  data () {
    return {
      code: '',
      bookComments: [{name: 'carl', text: 'you right you right'}]
    }
  }
}
</script>

